<template>
  <div v-if="isLoggedIn">
    <div id="a">
    <div class="container">
      <div class="centered-container">
        <h2></h2><br>
      </div>
      <div class="centered-container">
      <div class="search-bar">
        <input type="text" v-model="searchQuery" class="custom-input" placeholder="">
        <select v-model="selectedCategory"  id="search_kind">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
        </select>
        <button @click="searchGoods" style="width:130px" class="custom-button"></button>
      </div>
      </div>

      <table border="1px" align=center cellspacing="0">
        <tr>
          <th>ID</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="good in paginatedGoods" :key="good.goodid">
            <td>{{ good.goodid }}</td>
            <td>{{ good.goodname }}</td>
            <td>{{ good.description }}</td>
            <td>{{ good.price }}</td>
            <td> 
                <img :src="good.image" alt="" style="width: 100px; height: auto;" />
            </td>
            <td>{{ good.kind }}</td>
            <td>{{ good.subkind }}</td>
            <td>{{ good.creationDate }}</td>
        </tr>
      </table>

      <!--  -->
      <div class="pagination">
        <button @click="goToPrevPage" class="prev" :disabled="currentPage === 1"></button>
        <span id="page-info"> {{ currentPage }} / {{ totalPages }} </span>
        <button @click="goToNextPage" class="next" :disabled="currentPage === totalPages"></button>
      </div>
    </div>
    </div>
  </div>
  <div v-else class="else">
    <a href="/login"></a>
  </div>
</template>

<script>
import foodImage1 from '@/assets/img/buyer/food-1.jpg';
import foodImage2 from '@/assets/img/buyer/food-2.jpg';
export default {
  data() {
    return {
      isLoggedIn: true, // 
      products: [],
      selectedGood: null, // 
      selectedCategory: '', // 
      currentPage: 1,
      pageSize: 2, // 
      goods: [
        {
          "goodid": 1,
          "goodname": " - ",
          "description": "",
          "price": 150.0,
          "image": foodImage1,
          "kind": "",
          "subkind": "",
          "creationDate": "2023-01-15"
        },
        {
          "goodid": 2,
          "goodname": " - ",
          "description": "",
          "price": 50.0,
          "image": foodImage2,
          "kind": "",
          "subkind": "",
          "creationDate": "2023-02-10"
        },
        {
          "goodid": 3,
          "goodname": " - ",
          "description": "",
          "price": 200.0,
          "image": foodImage1,
          "kind": "",
          "subkind": "",
          "creationDate": "2023-03-05"
        }
      ], // 
      filteredGoods: [], // 
    };
  },
  methods: {
    searchGoods() {
        console.log('');
        this.filteredGoods = this.goods.filter((good) => {
            const matchesQuery = this.searchQuery ? good.goodname.toLowerCase().includes(this.searchQuery.toLowerCase()) : true;
            const matchesCategory = this.selectedCategory ? good.kind === this.selectedCategory : true;
            this.resetPage();
            return matchesQuery && matchesCategory;
        });
        // 
    },
    goToPrevPage() {
      // 
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    goToNextPage() {
      // 
        if (this.currentPage < this.totalPages) {
            this.currentPage++;
        }
    },
    // 
    resetPage() {
        this.currentPage = 1;
    },
    fetchProducts() {
      // 
    }
  },
  computed: {
    // 
    paginatedGoods() {
        const start = (this.currentPage - 1) * this.pageSize;
        const end = start + this.pageSize;
        return this.filteredGoods.slice(start, end);
    },
    // 
    totalPages() {
        return Math.ceil(this.filteredGoods.length / this.pageSize);
    }
  },
  watch: {
    // 
    filteredGoods(newValue, oldValue) {
        if (newValue !== oldValue) {
            this.resetPage();
        }
    }
  },
  mounted() {
    console.log('ShowHistoryGoods ');
    // 
    //  showSuccessModal
    this.filteredGoods = this.goods; // 
  }
};
</script>

